@charset "UTF-8";
body {
  font-family: "";
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.inner {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1150px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.test-bg {
  background-color: #ebebeb;
}

.c-btn-slide {
  background-repeat: no-repeat;
  background-size: cover;
  width: 340px;
  height: 100px;
  padding-left: 2em;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  color: #999;
  border: 1px solid #999;
  opacity: 1;
  text-decoration: none;
  position: relative;
  color: #023178;
  border-color: #023178;
}

.c-btn-slide::before {
  content: "";
  width: 0;
  height: 100%;
  display: block;
  background: rgba(0, 0, 0, 0.25);
  position: absolute;
  left: 0;
  top: 0;
  transition: 0.2s;
}

.c-btn-slide::after {
  content: "＞";
  font-size: 1.1em;
  position: absolute;
  right: 0.9em;
  top: 50%;
  transform: translateY(-50%);
  letter-spacing: 0;
  transition: 0.2s;
}

.c-btn-slide:hover::before {
  width: 100%;
}

.c-btn-wrap {
  margin: 5rem;
}

.c-hamburger {
  display: block;
  width: 3.75rem;
  height: 3.75rem;
  position: relative;
  border-radius: 6.25rem;
  color: white;
  background-color: white;
  z-index: 60;
}
.c-hamburger div {
  height: 0.125rem;
  width: 2.6875rem;
  background-color: white;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  transition: all 0.3s;
}

.c-hamburger div:nth-of-type(1) {
  top: 43%;
  width: 1.75rem;
}

.c-hamburger div:nth-of-type(2) {
  top: 61%;
  width: 1.5rem;
  transform: translate(-50%, -50%);
}

.c-hamburger.active div:nth-of-type(1) {
  transform: rotate(45deg) translate(-50%, -50%);
  transform-origin: 0% 50%;
  top: 50%;
  left: 50%;
  width: 1.5rem;
}

.c-hamburger.active div:nth-of-type(2) {
  transform: rotate(-45deg) translate(-50%, -50%);
  transform-origin: 0% 50%;
  top: 50%;
  left: 50%;
  width: 1.5rem;
}

.c-hamburger .close,
.c-hamburger.active .open {
  opacity: 0;
}